<template>
  <v-container>
    <h1>Lista de libros</h1>
    <v-divider></v-divider>
    <v-row justify="center">
      <v-col cols="12" md="3" v-for="book in books" :key="book.id">
        <v-card class="mx-auto" max-width="344">
          <v-img
            :src="book.image"
            height="200px"
          ></v-img>

          <v-card-title> {{book.title}} </v-card-title>

          <v-card-actions>
            <v-btn block color="primary" @click="$router.push(`/book/${book.id}`)">Ver mas</v-btn>
          </v-card-actions>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import { mapActions, mapState } from "vuex";

export default {
  name: "Home-view",

  components: {},
  computed: {
    ...mapState(["books"]),
  },
  methods: {
    ...mapActions(["fetchBooks"]),
  },
  created() {
    this.fetchBooks();
  },
};
</script>
